import {
  _decorator,
  Component,
  find,
  instantiate,
  Label,
  Node,
  Prefab,
  randomRange,
  randomRangeInt,
} from "cc";
const { ccclass, property } = _decorator;

@ccclass("NewComponent")
export class NewComponent extends Component {
  @property([String])
  PropenrtyNameArray: string[] = [];
  @property(Prefab)
  propentrylabel: Prefab = null;
  start() {
    this.schedule(function () {
      this.qiyong();
    }, 0.5);
  }

  update(deltaTime: number) {
    let NodePosition = this.node.getPosition();
    if (NodePosition.x < -1) {
      this.node.destroy;
    }
  }
  /**
   * 获取一个新的属性名
   * @returns
   */
  getname(): string {
    let num = randomRangeInt(0, this.PropenrtyNameArray.length);
    console.log(this.PropenrtyNameArray[num]);
    return this.PropenrtyNameArray[num];
  }
  qiyong() {
    let LabelNode = instantiate(this.propentrylabel);
    LabelNode.getChildByName("PropenrtyLabel").getComponent(Label).string =
      this.getname();
    LabelNode.parent = find("Canvas/PropList");
    LabelNode.setPosition(randomRange(-320, 320), 670);
  }
}
